import './index.css';
import React, { Component } from 'react';
import { NavLink, Switch, Route } from 'react-router-dom';
import Home from "./home";
import Projects from "./project";
import Resume from "./resume";
import Blog from "./blog";

function App() {
    return (
        <div className='app'>
            <MenuBar />
            <Main />
            <Footer />
        </div>
    )
}

class MenuBar extends Component {
    render() {
        return (
            <ol className="menu-bar">
                <li><NavLink exact activeClassName="current-menu" to='/'>Home</NavLink></li>
                <li><NavLink exact activeClassName="current-menu" to='/resume'>Resume</NavLink></li>
                <li><NavLink exact activeClassName="current-menu" to='/projects'>Projects</NavLink></li>
                <li><NavLink exact activeClassName="current-menu" to='/blog'>Blog</NavLink></li>
            </ol>
        );
    }
}

const Main = () => (
    <Switch>
        <Route exact path='/' component={Home}></Route>
        <Route exact path='/resume' component={Resume}></Route>
        <Route exact path='/projects' component={Projects}></Route>
        <Route exact path='/blog' component={Blog}></Route>
    </Switch>
)

const Footer = () => {
    return (
        <div className="footer">
            <div class="footer-text">Portfolio Website created by Victor Zhang</div>
        </div>
    );
}

export default App;